	module.exports = {"data":{"post":{"html":"<p>It worked! :-) At least 95% worked, and the few problems could be easily fixed. Two notes:</p>\n<p>1. We decided to skip domain mapping, since we don't have a group for all Samba users, and Samba 3 only maps one-to-one. </p>\n<p>2. To convert the SIDs, I exported the smbpasswd to XML, added line breaks, ran this perl script, and imported into samtdb.</p>\n<p>:<br />\n    #!/usr/bin/perl<br />\n    while (<>) {<br />\n        if (/sid=/) {<br />\n                /-(d+)\"/;<br />\n                my $oldrid=$1;<br />\n                my $newrid= (($oldrid-1000)*2)+1000 ;<br />\n                s/-d+\"/-$newrid\"/;<br />\n        }<br />\n        print;<br />\n    }</p>","fields":{"title":"Samba 3... yes!","date":"07 Apr 2004","url":"https://papascott.de/2004-04-07-samba-3-yes/"}}},"pathContext":{"slug":"/2004-04-07-samba-3-yes/","prev":{"fields":{"title":"Smarty Templates and Movable Type","date":"06 Apr 2004","slug":"/2004-04-06-smarty-templates-and-movable-type/"}},"next":{"fields":{"title":"Crossroads","date":"08 Apr 2004","slug":"/2004-04-08-crossroads/"}}}}

